<template>
    <div>
        <div class="body_menu_user">
            
        </div>

        <div class="search">
            <el-input class="searchInput" 
            v-model="searchText"
            placeholder=""
            prefix-icon="el-icon-search"
            @input="searchUser(searchText)">
            </el-input>
        </div>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            
            <el-table-column
            
                prop="id"
                label=""
                width="180">
            </el-table-column>
            
            
            <el-table-column
                prop="username"
                label=""
                width="180">
            </el-table-column>


            <el-table-column
                prop="password"
                label="">
            </el-table-column>

             <el-table-column label="">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)"></el-button>
                    </template>
            </el-table-column>

            
    </el-table>
    <div class="block">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div>  
    </div>
</template>
<script>
export default {
    data() {
        return {
            tableData: [
                
            ],
            searchText: '',
            currentPage: 1,
            total: 1,
            pageSize: 1,
            sendFlag: false,
             
            
        }
    },
    methods: {

            //
            handleEdit(index, row) {
                console.log(index, row);
                
                this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                
                }).then(({ value }) => {
                    this.$http.get('updateUser/'+row.id+'/'+row.username+'/'+value).then(res=>{
                        this.$router.go(0);
                    });
                    this.$message({
                        type: 'success',
                        message: ': ' + value
                    });
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });       
                });


            },
            handleDelete(index, row) {
                console.log(":"+row.id);
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                    }).then(() => {
                        this.$http.get('delUser/'+row.id).then(res =>{
                        if(res.data == 'ok') {
                            this.$message.success('!');
                            this.$router.go(0);
                        }
                        });
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                    });
                
            },

            //
            searchUser(text){
                console.log(""+text);
                this.$http.get('userFindLike/'+text).then(res =>{
                    this.currentPage = res.data.currentPage;
                    this.total = res.data.total;
                    this.pageSize = res.data.pageSize;
                    this.tableData = res.data.slist;
            
                })

            },

        //
        handleSizeChange(val) {
            
        },
        handleCurrentChange(val) {
            if(!this.sendFlag){
                this.$http.get('userinfo/'+this.pageSize+'/'+val).then(res =>{
                this.currentPage = res.data.currentPage;
                this.total = res.data.total;
                this.pageSize = res.data.pageSize;
                this.tableData = res.data.slist;
            
            })
            }
        }
        
        },
    created(){
        
        // this.$http.get('').then(res=>{

        // }).catch(err=>{

        // })


        //
        this.$http.get('userinfo/10/1').then(res => {
            
            console.log(res.data);
            this.currentPage = res.data.currentPage;
            this.total = res.data.total;
            this.pageSize = res.data.pageSize;
            this.tableData = res.data.slist;
            
        }).catch(err => {
    	    console.log(err);
        });

         
    }
}
</script>
<style>
    .body_menu_user{
        
        float: left;
        font-size: 30px;
        font-weight: bold;
        background: rgb(228, 228, 228);
  
    }
    
    .search{
        padding-top: 10px;
        margin-right: 20px;
        height: 50px;
        width: 400px;
        float: right;
        
    }
    .block{
        background-color: rgb(228, 230, 230);
    }
</style>